<template>
  <div class="video">
    <el-row style="margin-bottom: 10px;">
      选择播放地址
      <el-select @change="changechanne()" v-model="value" style="margin-left: 20px;" size="mini" placeholder="请选择">
          <el-option
            v-for="(item,index) in options"
            :key="index"
            :label="item"
            :value="item">
          </el-option>
        </el-select>
    </el-row>
    <el-row>
      <el-col :span="13">
        <div v-if="url==false" style="width: 100%;height: 420px;background: #999999;"></div>
        <div v-else id="video"></div>
      </el-col>
      <el-col :span="11" style="padding-left: 30px;">
        <p class="title">当前直播课程：<span>{{name}}</span></p>
        <p class="time"><span>当前直播预计结束时间：{{end}}</span>下场直播：{{nextProgram}}</p>
        <p class="lists"><span>持续时长：{{continuous}}</span>启动时间：{{start}}</p>
        <p class="lists"><span>文件类型：{{type}}</span>文件大小：{{size}}</p>
        <p class="lists"><span>文件时间：{{time}}</span>码率信息：{{rate}}</p>
        <p class="lists"><span>分辨率：{{resolution}}</span></p>
        <p class="btns"><button class="stop" @click="stop()" :disabled="stopdisabled">停止</button><button class="next" @click="next()">下一个</button></p>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  export default {
    name: 'video',
    data() {
      return {
        stopdisabled:true,
        value:'',
        options:[],
        url:'',
        channelid:'',
        name:'无',
        end:'——',
        nextProgram:'——',
        continuous:'——',
        start:'——',
        type:'——',
        size:'——',
        time:'——',
        rate:'——',
        resolution:'——',
        id:''
      }
    },
    computed:{
      listData() {
        return this.$store.state.channelid;
      }
    },
    watch:{
      listData(val) {
        this.channelid=val
        this.options.length=0
        this.getchanne()
        this.getdata()
      }
    },
    methods: {
      //修改播放地址
      changechanne(){
        this.ckplayera(this.value)
      },
      //获取频道信息
      getchanne() {
        this.$http({
          // url: '/proxyApi/a/bi/diy/catalog',
          url: this.$http.adornUrl('/api/channel/get?id='+this.channelid),
          method: 'POST'
        }).then(({
          data
        }) => {
          let msg = data.data
          if(msg.playUrlOne!=''){
            this.value=msg.playUrlOne
            this.options.push(msg.playUrlOne)
          }else if(msg.playUrlTwo!=''){
            this.value=msg.playUrlTwo
          }else if(msg.playUrlThree!=''){
            this.value=msg.playUrlThree
          }
          if(msg.playUrlTwo!=''){
            this.options.push(msg.playUrlTwo)
          }
          if(msg.playUrlThree!=''){
            this.options.push(msg.playUrlThree)
          }
        })
      },
      getdata(){
        this.$http({
          // url: '/proxyApi/a/bi/diy/catalog',
          url: this.$http.adornUrl('/api/scheduleProgram/getLivingProgram'),
          method: 'POST'
        }).then(({
          data
        }) => {
          console.log(data)
          let msg = data.data.livingProgram
          console.log(msg)
          if(data.data.nextProgram==''){
            this.nextProgram='——'
          }else{
            this.nextProgram=data.data.nextProgram.name
          }
          if(msg==''){
            this.name='无'
            this.end='——'
            this.continuous='——'
            this.start='——'
            this.type='——'
            this.size='——'
            this.time='——'
            this.rate='——'
            this.resolution='——'
            this.url=false
            this.stopdisabled=true
          }else{
            // if(msg.videoStatus==-1){
            //   var types='等待转码'
            // }else if(msg.videoStatus==0){
            //   var types='转码中'
            // }else if(msg.videoStatus==2){
            //   var types='转码失败'
            // }else if(msg.videoStatus==1){
            //   var types='待直播'
            // }else if(msg.videoStatus==3){
            //   var types='直播中'
            // }else if(msg.videoStatus==4){
            //   var types='已直播'
            // }
            if(msg.format==1){
              var types='flv'
            }else if(msg.format==2){
              var types='m3u8'
            }else if(msg.format==4){
              var types='mp4'
            }
            this.id=msg.id
            this.name=msg.name
            this.end='——'
            this.continuous=msg.duration
            this.start=msg.startTime
            this.type=types
            this.size=msg.fileSize
            this.time=msg.duration
            this.rate=msg.bitRateInformation
            this.resolution=msg.resolution
            this.url=true
            this.stopdisabled=false
            this.ckplayera(this.value)
          }
        })
      },
      stop() {
        this.$http({
          url: 'http://edulivetestapi.3xmt.com/transcoding/stop/' + this.id,
          // url: this.$http.adornUrl('/transcoding/stop/' + id),
          method: 'GET'
        }).then(({
          data
        }) => {
          // console.log(data)
          if (data.code == 0) {
            this.getdata()
            this.$message({
              type: 'success',
              message: '已停止课程!'
            });
          } else {
            this.$message.error(data.msg);
          }
        })
      },
	  next(){
		  this.$http({
		    url: 'http://edulivetestapi.3xmt.com/transcoding/next/' + this.id,
		    // url: this.$http.adornUrl('/transcoding/stop/' + id),
		    method: 'GET'
		  }).then(({
		    data
		  }) => {
		    console.log(data)
		    if (data.code == 0) {
		      this.getdata()
		      this.$message({
		        type: 'success',
		        message: '已停止课程!'
		      });
		    } else {
		      this.$message.error(data.message);
		    }
		  })
	  },
      //ckplayer播放器
      ckplayera(url) {
        setTimeout(function() {
          var videoObject = {
            container: '#video', // 容器的ID或className
            variable: 'player', // 播放函数名称 与下面一致
            loaded: 'loadedHandler',
            autoplay: false, // 是否自动播放
            live: true, // 是否是直播视频 true = 直播，false = 点播
            video: url // hls地址
          }
          let player = new ckplayer(videoObject)
          function loadedHandler() {
            player.videoMute();
            player.addListener('time', timeHandler); //监听播放时间
          }
          function timeHandler(time) {
            if (time > 0) {
              player.removeListener('time', timeHandler); //移除监听播放时间
              player.videoPause(); //暂停
              player.videoEscMute();
            }
          }
        }, 2)
      }
    },
    created() {},
    mounted() {},
    components: {},
    destroyed() {}
  }
</script>

<style scoped lang="scss">
  .video {
    margin: 20px 0 0;
    position: relative;
    overflow: hidden;

    #video {
      width: 100%;
      height: 420px;
    }

    .title {
      color: #333333;
      font-size: 20px;
      margin-bottom: 10px;

      span {
        color: #42BCDB;
      }
    }

    .time {
      color: #42BCDB;
      margin-bottom: 20px;

      span {
        display: inline-block;
        width: 50%;
      }
    }

    .lists {
      margin-bottom: 13px;

      span {
        display: inline-block;
        width: 50%;
      }
    }

    .btns {
      position: absolute;
      bottom: 5px;

      button {
        border: none;
        outline: none;
        color: #ffffff;
        cursor: pointer;
      }

      .stop {
        width: 120px;
        height: 40px;
        background: rgba(219, 66, 66, 1);
        border-radius: 4px;
        margin-right: 60px;
      }

      .next {
        width: 120px;
        height: 40px;
        background: rgba(66, 188, 219, 1);
        border-radius: 4px;
      }
    }
  }
</style>



