<template>
  <div class="content">
    <el-row>
      <el-col :span="3" style="min-height: 300px;">
        <left></left>
      </el-col>
      <el-col :span="21" style="padding-left: 20px;">
        <site></site>
        <videoChat></videoChat>
        <list></list>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  import left from './manages/left'
  import site from './manages/site'
  import videoChat from './manages/video'
  import list from './manages/list'
  export default {
    name: 'manage',
    data() {
      return {}
    },
    methods: {},
    created() {},
    mounted() {},
    components: {
      left,
      site,
      videoChat,
      list
    },
    destroyed() {}
  }
</script>

<style lang="scss">
  .content{
    padding: 20px 20px 40px;
    background: #FFFFFF;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    >div{
      width: 100%;
      height: 100%;
      >div{
        height: 100%;
      }
    }
  }
</style>



