<template>
  <div class="left">
    <div>
      <ul>
        <!-- <li class="now">频道1</li> -->
        <li v-for="(item,index) in list" @click="changeChanne(item.id)" :key="index" :class="{now:item.id==channelid}">{{item.name}}</li>
      </ul>
      <button class="add" @click="add">添加频道</button>
    </div>
    <el-dialog title="新增频道" :visible.sync="addchannel" :show-close="false" :close-on-click-modal="false">
      <div class="addinputs">
        <el-row>
          <el-col :span="13" style="padding-right: 8.32%;">频道名称 <el-input v-model="name" size="mini" style="width:calc(100% - 81px);margin-left: 20px;"></el-input>
          </el-col>
          <el-col :span="11"></el-col>
        </el-row>
        <el-row>
          <el-col :span="13" style="padding-right: 8.32%;">推流地址 <el-input v-model="pushUrlOne" size="mini" style="width:calc(100% - 81px);margin-left: 20px;"></el-input>
          </el-col>
          <el-col :span="11">播放地址 <el-input v-model="playUrlOne" size="mini" style="width:calc(100% - 81px);margin-left: 20px;"></el-input>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="13" style="padding-right: 8.32%;">推流地址 <el-input v-model="pushUrlTwo" size="mini" style="width:calc(100% - 81px);margin-left: 20px;"></el-input>
          </el-col>
          <el-col :span="11">播放地址 <el-input v-model="playUrlTwo" size="mini" style="width:calc(100% - 81px);margin-left: 20px;"></el-input>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="13" style="padding-right: 8.32%;">推流地址 <el-input v-model="pushUrlThree" size="mini" style="width:calc(100% - 81px);margin-left: 20px;"></el-input>
          </el-col>
          <el-col :span="11">播放地址 <el-input v-model="playUrlThree" size="mini" style="width:calc(100% - 81px);margin-left: 20px;"></el-input>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="13" style="padding-right: 8.32%;">
            水印图片
            <img v-if="imageUrl" :src="imageUrl" :picid="picid" class="avatar">
            <span v-else class="avatar" style="border: 1px solid #d9d9d9;">
              <i class="el-icon-plus avatar-uploader-icon"></i>
            </span>
          </el-col>
          <el-col :span="11">
            <el-upload class="avatar-uploader" action="http://localhost:8080/hiswvideoweb_war_exploded/api/pic/upload" :show-file-list="false"
              :on-success="handleAvatarSuccess" :before-upload="beforeAvatarUpload">
              <el-button size="small" type="primary">更换</el-button>
            </el-upload>
          </el-col>
        </el-row>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-row>
          <el-col :span="13" style="height: 10px;"></el-col>
          <el-col :span="11">
            <el-button type="info" size="mini" style="float: left;" @click="call()">取 消</el-button>
            <el-button type="primary" size="mini" @click="save">确 定</el-button>
          </el-col>
        </el-row>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import bus from './middle'
  export default {
    name: 'left',
    data() {
      return {
        channelid: '',
        addchannel: false,
        name: '',
        picid: '',
        pushUrlOne: '',
        playUrlOne: '',
        pushUrlTwo: '',
        playUrlTwo: '',
        pushUrlThree: '',
        playUrlThree: '',
        imageUrl: '',
        list: []
      }
    },
    methods: {
      //点击添加频道
      add() {
        this.addchannel = true
      },
      //图片上传回调
      handleAvatarSuccess(res, file) {
        this.picid = file.response.data.id
        this.imageUrl = URL.createObjectURL(file.raw);
      },
      //图片上传判断格式
      beforeAvatarUpload(file) {
        // console.log(file.type)
        const isJPG = file.type === 'image/jpeg';
        const isPNG = file.type === 'image/png';
        const isGIF = file.type === 'image/gif';
        const isBMP = file.type === 'image/bmp';
        const isLt2M = file.size / 1024 / 1024 < 2;

        if (isJPG||isPNG||isGIF||isBMP) {
          var next=true
        }else{
          var next=false
          this.$message.error('上传图片只能是 JPG、PNG、GIF、BMP 格式!');
        }
        return next;
      },
      //请求频道列表
      getList() {
        this.list.length = 0
        this.$http({
          // url: '/proxyApi/api/channel/list',
          url: this.$http.adornUrl('/api/channel/list'),
          method: 'POST'
        }).then(({
          data
        }) => {
          this.channelid = data.data[0].id
          this.$store.commit('savechannelid', data.data[0].id)
          this.list = data.data
        })
      },
      //切换频道
      changeChanne(id) {
        this.channelid = id
        this.$store.commit('savechannelid', id)
      },
      //点击取消
      call(){
        this.name=''
        this.picid=''
        this.pushUrlOne=''
        this.playUrlOne=''
        this.pushUrlTwo=''
        this.playUrlTwo=''
        this.pushUrlThree=''
        this.playUrlThree=''
        this.imageUrl=''
        this.addchannel = false
      },
      //点击新增频道保存
      save() {
        this.$http({
          // url: '/proxyApi/api/channel/save',
          url: this.$http.adornUrl('/api/channel/save?name=' + this.name + '&picid=' + this.picid + '&pushUrlOne=' +
            this.pushUrlOne + '&playUrlOne=' + this.playUrlOne +
            '&pushUrlTwo=' + this.pushUrlTwo + '&playUrlTwo=' + this.playUrlTwo + '&pushUrlThree=' + this.pushUrlThree +
            '&playUrlThree=' + this.playUrlThree),
          method: 'POST'
        }).then(({
          data
        }) => {
          if (data.code == 0) {
            this.getList()
            this.$message({
              message: '添加频道成功',
              type: 'success',
              onClose: function() {
                this.name=''
                this.picid=''
                this.pushUrlOne=''
                this.playUrlOne=''
                this.pushUrlTwo=''
                this.playUrlTwo=''
                this.pushUrlThree=''
                this.playUrlThree=''
                this.imageUrl=''
                this.addchannel = false
              }
            });
            this.addchannel = false
          } else {
            this.$message.error(data.msg);
          }
        })
      }
    },
    created() {
      bus.$on('getchannel',(item)=>{
        this.getList()
      })
    },
    mounted() {
      this.getList()
    },
    components: {},
    destroyed() {}
  }
</script>

<style scoped lang="scss">
  .left {
    background: #F2F2F2;
    padding: 20px;
    height: 100%;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 87.5%;
    box-sizing: border-box;
    border-radius: 8px;
    overflow: hidden;

    >div {
      height: 100%;
      width: calc(100% + 35px);
      overflow-x: hidden;
      overflow-y: scroll;

      ul {
        list-style: none;
        padding-right: 20px;

        li {
          font-size: 16px;
          font-family: Microsoft YaHei;
          font-weight: 400;
          color: rgba(102, 102, 102, 1);
          padding: 8px;
          white-space: nowrap;
          text-overflow: ellipsis;
          overflow: hidden;
          cursor: pointer;
          border-radius: 4px;
        }

        .now {
          background: rgba(66, 188, 219, 1);
          color: #ffffff;
        }
      }

      .add {
        width: calc(100% - 20px);
        border: none;
        outline: none;
        height: 36px;
        background: rgba(66, 188, 219, 1);
        border-radius: 4px;
        color: #ffffff;
        cursor: pointer;
      }

      .avatar {
        display: inline-block;
        text-align: center;
        line-height: 100px;
        width:calc(100% - 83px);
        height: 100px;
        border-radius: 4px;
        vertical-align: top;
        margin-left: 20px;
      }

      .avatar-uploader {
        display: inline-block;
      }
    }
  }
</style>
<style lang="scss">
  .addinputs {
    .el-row {
      margin-bottom: 20px;
    }
  }
</style>



