<template>
  <div class="list">
    <div class="date">
      选择日期
      <el-date-picker v-model="selectTiem" style="margin-left: 20px;" @change="getList()" type="date" size="mini"
        placeholder="选择日期">
      </el-date-picker>
    </div>
    <el-table :data="tableData" style="width: 100%" :header-cell-style="{background:'#F2F2F2'}">
      <el-table-column prop="name" label="课程名称">
      </el-table-column>
      <el-table-column prop="coverHttpUrl" label="海报封面" width="180">
        <template slot-scope="scope">
          <img :src="scope.row.coverHttpUrl" alt="" start="">
        </template>
      </el-table-column>
      <el-table-column prop="startTime" label="启动时间">
      </el-table-column>
      <el-table-column prop="length" label="持续时长">
      </el-table-column>
      <el-table-column prop="videoStatus" label="状态">
        <template slot-scope="scope">
          <div style="text-align: right;">
            <span v-if="scope.row.videoStatus==-1">等待转码</span>
            <span v-if="scope.row.videoStatus==0">转码中</span>
            <span v-if="scope.row.videoStatus==2">转码失败</span>
            <span v-if="scope.row.videoStatus==1">待直播</span>
            <span v-if="scope.row.videoStatus==3">直播中</span>
            <span v-if="scope.row.videoStatus==4">已直播</span>
          </div>
        </template>
      </el-table-column>
      <el-table-column prop="fileSize" label="文件大小">
      </el-table-column>
      <el-table-column prop="address" style="text-align: right;" :render-header="renderHeader" width="400">
        <template slot-scope="scope">
          <div style="text-align: right;">
            <el-button type="primary" size="mini" @click="details(scope.row.id)">详情</el-button>
            <el-button type="primary" size="mini" v-if="scope.row.videoStatus==null" @click="start(scope.row.id)">启动</el-button>
            <el-button type="primary" size="mini" v-if="scope.row.videoStatus==3" @click="stop(scope.row.id)">停止</el-button>
            <el-button type="primary" size="mini" @click="preview(scope.row.playHttpUrl)">点播预览</el-button>
            <el-button type="danger" size="mini" @click="delect(scope.row.id)">删除</el-button>
          </div>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog title="详情" :visible.sync="particulars" width="760" :show-close="false">
      <div>
        <el-row>
          <el-col :span="13" style="padding-right: 8.32%;">课程名称 <el-input v-model="detail.name" size="mini" style="width:calc(100% - 81px);margin-left: 20px;"
              placeholder="请输入内容"></el-input>
          </el-col>
          <el-col :span="11">启动时间
            <el-time-picker v-model="detail.selectstartTime" :picker-options="{
                selectableRange: '00:00:00 - 23:59:59'
              }"
              @change="changestarttime()" size="mini" style="width:calc(100% - 81px);margin-left: 20px;" placeholder="选择时间"></el-time-picker>
          </el-col>
        </el-row>
        <!-- <div id="video2"></div> -->
        <img :src="detail.coverHttpUrl" style="width: 720px;height: 520px;" alt="">
        <el-row class="details-list">
          <el-col :span="8">持续时长：{{detail.length}}</el-col>
          <el-col :span="8">启动时间：{{detail.startTime}}</el-col>
          <el-col :span="8">文件类型：{{detail.filetype}}</el-col>
        </el-row>
        <el-row class="details-list">
          <el-col :span="8">文件大小：{{detail.fileSize}}</el-col>
          <el-col :span="8">文件时间：{{detail.length}}</el-col>
          <el-col :span="8">码率信息：{{detail.bitRate}}</el-col>
        </el-row>
        <el-row class="details-list">
          <el-col :span="8">分辨率：{{detail.resolution}}</el-col>
          <el-col :span="8"></el-col>
          <el-col :span="8"></el-col>
        </el-row>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="info" size="mini" @click="particulars = false">取 消</el-button>
        <el-button type="primary" size="mini" @click="changecourse()">保 存</el-button>
      </span>
    </el-dialog>
    <el-dialog :visible.sync="previews" width="760" class="preview" :show-close="false">
      <div slot="title" style="text-align: center;background: #42BCDB;padding: 0;color: #ffffff;">
        点播预览
        <i class="el-icon-circle-close" @click="previews = false" style="cursor: pointer;float: right;font-size: 30px;margin-top: 10px;margin-right: 10px;"></i>
      </div>
      <div id="video3"></div>
      <!-- <span slot="footer" class="dialog-footer">
        <el-button size="mini" @click="previews = false">取 消</el-button>
        <el-button type="primary" size="mini" @click="previews = false">保 存</el-button>
      </span> -->
    </el-dialog>
    <el-dialog title="新增排片" :visible.sync="addSchedule" width="760" :show-close="false" :close-on-click-modal="false">
      <div>
        <el-row>
          <el-col :span="13" style="padding-right: 8.32%;">课程名称 <el-input v-model="courseName" size="mini" style="width:calc(100% - 81px);margin-left: 20px;"></el-input>
          </el-col>
          <el-col :span="11">启动时间 <el-time-picker v-model="startupTime" :picker-options="{
                selectableRange: '00:00:00 - 23:59:59'
              }"
              size="mini" style="width:calc(100% - 81px);margin-left: 20px;" placeholder="选择时间">
            </el-time-picker>
          </el-col>
        </el-row>
        <el-row style="margin-top: 20px;">
          <el-col :span="13" style="padding-right: 8.32%;">选择日期<el-date-picker v-model="courseTime" size="mini" style="width:calc(100% - 81px);margin-left: 23px;"
              type="date" placeholder="选择日期"></el-date-picker>
          </el-col>
        </el-row>
        <el-row style="margin-top: 20px;">
          <el-upload style="display:inline-block"
          class="avatar-uploader" action="string" :show-file-list="false"
            :http-request="handleUpload" accept=".mp4,.flv,.avi,.wmv,.rmvb">
            <el-button size="mini" type="primary">上传文件</el-button>
          </el-upload>
          <el-input size="mini" v-model="videoname" disabled="disabled" style="width: calc(100% - 110px);margin-left: 20px;"></el-input>
        </el-row>
        <el-row>
          <el-progress v-if="videoFlag == true" :percentage="videoUploadPercent" style="margin-top:30px;"></el-progress>
        </el-row>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="info" size="mini" :disabled="adddisabled" @click="call()">取 消</el-button>
        <el-button type="primary" size="mini" :disabled="adddisabled" @click="savenew()">保 存</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  import moment from 'moment'
  export default {
    name: 'list',
    data() {
      return {
        url:'',
        courseid: '',
        channelid: '',
        selectTiem: new Date(),
        tableData: [],
        particulars: false,
        previews: false,
        addSchedule: false,
        courseName: '',
        startupTime: '',
        courseTime: '',
        filename: '',
        filesize: '',
        videoFlag: false,
        adddisabled:false,
        videoUploadPercent: '',
        videoname:'',
        videoForm: {
          Video: '',
        },
        newvideoid:'',
        // uploadUrl:'http://localhost:8080/hiswvideoweb_war_exploded/uploadOnLine/upload?filesize=3568&projectId=12&name=ppp',
        detail: {
          name: '',
          coverHttpUrl: '',
          videoid: '',
          channelId: '',
          startTime: '',
          date: '',
          coverHttpUrl: '',
          duration: '',
          videoStatus: '',
          fileSize: '',
          filetype: '',
          bitRateInformation: '',
          resolution: '',
          selectstartTime: ''
        }
      }
    },
    computed: {
      listData() {
        return this.$store.state.channelid;
      }
    },
    watch: {
      listData(val) {
        // console.log(val)
        this.channelid = val
        this.getList()
      }
    },
    methods: {
      handleUpload(params){
        const file = params.file;
        console.log(file)
        if (['video/mp4', 'video/flv','video/avi','video/wmv','video/rmvb'].indexOf(file.type) == -1) {
            this.$message.error('请上传正确的视频格式');
            return false;
        }
        var filesize=file.size,filename=file.name
        // 这里用params.action 其实也是一样的
        // 可以自己向url里面加点参数，一定要按自己需求来重写请求发送过程，这里是我用的版本的阉割版，毕竟有些东西不好写出来
        var formData = new FormData();
        formData.append("file", file);
        let _this=this
        this.$http({
          url: 'http://localhost:8080/hiswvideoweb_war_exploded/uploadOnLine/upload?filesize='+filesize+'&projectId=15&name='+filename,
          method: 'POST',
          data:formData,
          onUploadProgress:function(progressEvent){ //原生获取上传进度的事件
            var complete = (progressEvent.loaded / progressEvent.total * 100 | 0)
            _this.videoFlag = true;
            if(complete<100){
              _this.adddisabled=true
            }else{
              _this.adddisabled=false
            }
            _this.videoUploadPercent = complete;
          },
        }).then(({
          data
        }) => {
          if(data.status==true){
            this.videoname=data.filePath
            this.newvideoid=data.vid
            this.adddisabled=false
          }else{
            this.$message.error('上传失败');
            this.adddisabled=false
          }
        })
      },
      //表格头部按钮
      renderHeader(h) {
        let a = [
          h('el-button', {
            props: {
              type: 'primary',
              size: 'mini',
            },
            style: 'float:right',
            on: {
              click: () => {
                this.addSchedule = true
              }
            }
          }, '新增排片')
        ]
        return h('div', a)
      },
      //详情弹窗播放器
      ckplayera() {
        setTimeout(function() {
          var videoObject = {
            container: '#video2', // 容器的ID或className
            variable: 'player', // 播放函数名称 与下面一致
            loaded: 'loadedHandler',
            autoplay: false, // 是否自动播放
            live: true, // 是否是直播视频 true = 直播，false = 点播
            video: 'http://img.ksbbs.com/asset/Mon_1703/05cacb4e02f9d9e.mp4' // hls地址
          }
          let player = new ckplayer(videoObject)

          function loadedHandler() {
            player.videoMute();
            player.addListener('time', timeHandler); //监听播放时间
          }

          function timeHandler(time) {
            if (time > 0) {
              player.removeListener('time', timeHandler); //移除监听播放时间
              player.videoPause(); //暂停
              player.videoEscMute();
            }
          }
        }, 2)
      },
      //点播预览播放器
      previewVideo(url) {
        setTimeout(function() {
          var videoObject = {
            container: '#video3', // 容器的ID或className
            variable: 'player', // 播放函数名称 与下面一致
            loaded: 'loadedHandler',
            autoplay: false, // 是否自动播放
            live: true, // 是否是直播视频 true = 直播，false = 点播
            video: url // hls地址
          }
          let player = new ckplayer(videoObject)

          function loadedHandler() {
            player.videoMute();
            player.addListener('time', timeHandler); //监听播放时间
          }

          function timeHandler(time) {
            if (time > 0) {
              player.removeListener('time', timeHandler); //移除监听播放时间
              player.videoPause(); //暂停
              player.videoEscMute();
            }
          }
        }, 2)
      },
      //点击列表详情按钮
      details(id) {
        this.particulars = true
        this.courseid = id
        this.$http({
          // url: '/proxyApi/api/scheduleProgram/get',
          url: this.$http.adornUrl('/api/scheduleProgram/get?id=' + id),
          method: 'POST'
        }).then(({
          data
        }) => {
          // console.log('详情',data)
          this.detail = data.data

          this.detail.selectstartTime = new Date(moment(new Date()).format("YYYY-MM-DD") + ' ' + data.data.startTime)
        })
        this.ckplayera()
      },
      //点击列表点播预览
      preview(url) {
        this.previews = true
        this.previewVideo(url)
      },
      //获取列表内容
      getList() {
        this.$http({
          // url: '/proxyApi/api/scheduleProgram/list',
          // url: this.$http.adornUrl('/api/scheduleProgram/list?channelId=' + this.channelid + '&date=' + moment(this
          //   .selectTiem).format("YYYY-MM-DD")),
          url: 'http://localhost:8080/hiswvideoweb_war_exploded/uploadOnLine/upload?filesize=3568&projectId=12&name=ppp.mp4',
          method: 'POST'
        }).then(({
          data
        }) => {
          console.log('1231321213', data)
          // this.tableData = data.data
        })
      },
      //新增排片点击保存
      savenew() {
        this.$http({
          // url: '/proxyApi/api/scheduleProgram/save',
          url: this.$http.adornUrl('/api/scheduleProgram/save?name=' + this.courseName + '&startTime=' + moment(
              this.startupTime).format("HH:mm:ss") +
            '&videoid='+this.newvideoid+'&channelId=' + this.channelid + '&date=' + moment(this.courseTime).format("YYYY-MM-DD")
          ),
          method: 'POST'
        }).then(({
          data
        }) => {
          if (data.code == 0) {
            this.getList()
            this.addSchedule = false
            this.$message({
              message: '新增排片成功',
              type: 'success'
            });
            this.videoUploadPercent=''
            this.videoFlag=false
            this.videoname=''
            this.newvideoid=''
            this.courseName = ''
            this.startupTime = ''
            this.courseTime = ''
          } else {
            this.$message.error(data.msg);
          }
        })
      },
      //新增排片取消
      call() {
        this.videoname=''
        this.newvideoid=''
        this.courseName = ''
        this.startupTime = ''
        this.courseTime = ''
        this.addSchedule = false
        this.videoUploadPercent=''
        this.videoFlag=false
      },
      //点击排片详情保存
      changecourse() {
        this.$http({
          // url: '/proxyApi/api/scheduleProgram/save',
          url: this.$http.adornUrl('/api/scheduleProgram/update?name=' + this.detail.name + '&videoid=&channelId=' +
            this.channelid + '&startTime=' + this.detail.startTime + '&id=' + this.courseid + '&date=' + moment(
              this
              .selectTiem).format("YYYY-MM-DD")),
          method: 'POST'
        }).then(({
          data
        }) => {
          if (data.code == 0) {
            this.getList()
            this.particulars = false
            this.$message({
              message: '修改排片成功',
              type: 'success'
            });
          } else {
            this.$message.error(data.msg);
          }
        })
      },
      //点击删除排片
      delect(id) {
        this.$confirm('此操作将删除该课程, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$http({
            // url: '/proxyApi/api/scheduleProgram/save',
            url: this.$http.adornUrl('/api/scheduleProgram/delete?id=' + id),
            method: 'POST'
          }).then(({
            data
          }) => {
            if (data.code == 0) {
              this.getList()
              this.$message({
                type: 'success',
                message: '删除成功!'
              });
            } else {
              this.$message.error(data.msg);
            }
          })
        });
      },
      //点击启动
      start(id) {
        this.$http({
          url: 'http://edulivetestapi.3xmt.com/transcoding/start/' + id,
          // url: this.$http.adornUrl('/transcoding/start/' + id),
          method: 'GET'
        }).then(({
          data
        }) => {
          // console.log(data)
          if (data.code == 0) {
            this.getList()
            this.$message({
              type: 'success',
              message: '启动成功!'
            });
          } else {
            this.$message.error(data.msg);
          }
        })
      },
      //点击停止
      stop(id) {
        this.$http({
          url: 'http://edulivetestapi.3xmt.com/transcoding/stop/' + id,
          // url: this.$http.adornUrl('/transcoding/stop/' + id),
          method: 'GET'
        }).then(({
          data
        }) => {
          // console.log(data)
          if (data.code == 0) {
            this.getList()
            this.$message({
              type: 'success',
              message: '已停止课程!'
            });
          } else {
            this.$message.error(data.msg);
          }
        })
      }
    },
    components: {},
    created() {},
    mounted() {
      this.getList()
    },
    destroyed() {}
  }
</script>

<style scoped lang="scss">
  .list {
    margin-top: 20px;

    .date {
      margin-bottom: 20px;
    }

    .el-message-box__title {
      text-align: center;
    }

    .el-message-box__headerbtn {
      display: none;
    }

    .add {
      float: right;
    }
  }

  #video2 {
    width: 720px;
    height: 520px;
    margin: 20px 0;
  }
</style>
<style lang="scss">
  // .list{
  .el-dialog__header {
    text-align: center;
  }

  .details-list {
    margin-bottom: 20px;
  }

  .preview {
    .el-dialog__header {
      padding: 0;
      height: 48px;
      line-height: 48px;
    }

    .el-dialog__body {
      padding: 10px;
    }
  }

  // }
</style>



