<template>
  <div class="site">
    <el-row>
      <el-col :span="7" class="three">
        <div>
          <span>推流地址</span>
          <el-input v-model="pushUrlOne" style="width: calc(100% - 110px);">
          </el-input>
        </div>
        <div>
          <span>推流地址</span>
          <el-input v-model="pushUrlTwo" style="width: calc(100% - 110px);">
          </el-input>
        </div>
        <div>
          <span>推流地址</span>
          <el-input v-model="pushUrlThree" style="width: calc(100% - 110px);">
          </el-input>
        </div>
      </el-col>
      <el-col :span="7" class="three">
        <div>
          <span>播放地址</span>
          <el-input v-model="playUrlOne" style="width: calc(100% - 110px);">
          </el-input>
        </div>
        <div>
          <span>播放地址</span>
          <el-input v-model="playUrlTwo" style="width: calc(100% - 110px);">
          </el-input>
        </div>
        <div>
          <span>播放地址</span>
          <el-input v-model="playUrlThree" style="width: calc(100% - 110px);">
          </el-input>
        </div>
      </el-col>
      <el-col :span="7" class="watermark">
        <div>
          <span>水印图标</span>
          <img :src="picHttpUrl" :picid="picid" alt="">
        </div>
        <div>
          <span>频道名称</span>
          <el-input v-model="name" :channelid="id" style="width: calc(100% - 110px);">
          </el-input>
        </div>
      </el-col>
      <el-col :span="3" class="btns">
        <el-upload style="width: calc(100% - 20px);" class="avatar-uploader" action="http://localhost:8080/hiswvideoweb_war_exploded/api/pic/upload" :show-file-list="false"
          :on-success="handleAvatarSuccess" :before-upload="beforeAvatarUpload">
          <button style="width: 100%;">更换</button>
        </el-upload>
        <button @click="save()">保存</button>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  import bus from './middle'
  export default {
    name: 'site',
    data() {
      return {
        channelid:'',
        id: '',
        name: '',
        picid: '',
        picHttpUrl: '',
        pushUrlOne: '',
        playUrlOne: '',
        pushUrlTwo: '',
        playUrlTwo: '',
        pushUrlThree: '',
        playUrlThree: ''
      }
    },
    computed:{
      listData() {
        return this.$store.state.channelid;
      }
    },
    watch:{
      listData(val) {
        this.channelid=val
        this.getdata()
      }
    },
    methods: {
      //图片上传回调
      handleAvatarSuccess(res, file) {
        this.picid = file.response.data.id
        this.picHttpUrl = URL.createObjectURL(file.raw);
      },
      //图片上传判断格式
      beforeAvatarUpload(file) {
        const isJPG = file.type === 'image/jpeg';
        const isPNG = file.type === 'image/png';
        const isGIF = file.type === 'image/gif';
        const isBMP = file.type === 'image/bmp';
        const isLt2M = file.size / 1024 / 1024 < 2;

        if (isJPG||isPNG||isGIF||isBMP) {
          var next=true
        }else{
          var next=false
          this.$message.error('上传图片只能是 JPG、PNG、GIF、BMP 格式!');
        }
        return next;
      },
      //获取频道信息
      getdata() {
        this.$http({
          // url: '/proxyApi/a/bi/diy/catalog',
          url: this.$http.adornUrl('/api/channel/get?id='+this.channelid),
          method: 'POST'
        }).then(({
          data
        }) => {
          // console.log(data)
          let msg = data.data
          this.id = msg.id
          this.name = msg.name
          this.picid = msg.picid
          this.picHttpUrl = msg.picHttpUrl
          this.pushUrlOne = msg.pushUrlOne
          this.playUrlOne = msg.playUrlOne
          this.pushUrlTwo = msg.pushUrlTwo
          this.playUrlTwo = msg.playUrlTwo
          this.pushUrlThree = msg.pushUrlThree
          this.playUrlThree = msg.playUrlThree
        })
      },
      //
      save(){
        this.$http({
          // url: '/proxyApi/api/channel/update',
          url: this.$http.adornUrl('/api/channel/update?id='+this.id+'&name=' + this.name + '&picid=' + this.picid + '&pushUrlOne=' +
            this.pushUrlOne + '&playUrlOne=' + this.playUrlOne +
            '&pushUrlTwo=' + this.pushUrlTwo + '&playUrlTwo=' + this.playUrlTwo + '&pushUrlThree=' + this.pushUrlThree +
            '&playUrlThree=' + this.playUrlThree),
          method: 'POST'
        }).then(({
          data
        }) => {
          if(data.code==0){
            bus.$emit('getchannel',({i:1}))
            this.$message({
              message: '修改成功',
              type: 'success'
            });
          }else{
            this.$message.error(data.msg);
          }
        })
      }
    },
    created() {},
    mounted() {},
    components: {},
    destroyed() {}
  }
</script>

<style scoped lang="scss">
  .site {
    width: 100%;
    height: 180px;
    background: rgba(242, 242, 242, 1);
    border-radius: 8px;

    .three {
      height: 180px;

      >div {
        height: 33%;
        box-sizing: border-box;
        display: flex;
        align-items: center;

        span {
          display: inline-block;
          width:104px;
          text-align: center;
        }
      }
    }

    .watermark {
      height: 180px;

      span {
        display: inline-block;
        margin: 0 20px;
      }

      >div:first-of-type {
        height: 67%;
        box-sizing: border-box;
        padding-top: 20px;

        img {
          width: calc(100% - 110px);
          height: 100%;
          background: rgba(255, 255, 255, 1);
          border-radius: 4px;
          vertical-align: top;
        }
      }

      >div:last-of-type {
        height: 33%;
        box-sizing: border-box;
        display: flex;
        align-items: center;
      }
    }

    .btns {
      height: 180px;
      box-sizing: border-box;
      padding: 20px 0 20px 40px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      button {
        border: none;
        outline: none;
        width: calc(100% - 20px);
        height: 30px;
        background: rgba(66, 188, 219, 1);
        border-radius: 4px;
        color: #ffffff;
        cursor: pointer;
      }
    }
  }
</style>
<style lang="scss">
  .site{
    .btns{
      .el-upload{
        width: 100%;
      }
    }
  }
</style>



